import styled from 'styled-components';
import Container from 'react-bootstrap/Container';
import SideBar from '../components/SideBar';
import AuthInput from '../components/AccountForm/AuthInput';
import { AuthInputContainer } from '../components/common/auth.styled';
import { Button } from 'react-bootstrap';


// main
const MainStyle = styled.div`
  display: flex;
  gap: 1rem;
`

// sidebar
const LeftContainer = styled.div`
  flex: 5 1 0;
  padding: 1rem;
  min-height: 100vh;
  display: flex;
  flex-flow: column;
`

// 
const CenterContainer = styled.div`
  border-right: 1px solid var(--border-color);
  border-left: 1px solid var(--border-color);
  flex: 16 1 0;
  height: 1200px;
  display: flex;
  flex-direction: column;
`

// 
const RightContainer = styled.div`
  flex: 7 1 0;
`

// border-line
const BorderLine = styled.div`
  width: 100%;
  border-top: 1px solid #E6ECF0;
`

// authInput
const InputContainer = styled.div`
  width: 100%;
  margin: 0 auto;

  display: flex;
  flex-direction: column;
  align-items: center;
`


const SettingPage = () => {

  return (
    <Container>
      <MainStyle>
        
        {/*  */}
        <LeftContainer>
          <SideBar location={'home'}/>
        </LeftContainer>

        {/* - */}
        <CenterContainer>
          <h3 style={{ margin: '20px' }}></h3>
          <BorderLine/>

          {/*  */}
          <InputContainer>

            <AuthInputContainer style={{ width:'90%' }}>
              <AuthInput
                label=""
                placeholder=""
                // value=''
              />
            </AuthInputContainer>


            <AuthInputContainer style={{ width:'90%' }}>
              <AuthInput
                label=""
                placeholder=""
                // value=''
              />
            </AuthInputContainer>
          

            <AuthInputContainer style={{ width:'90%' }}>
              <AuthInput
                label="Email"
                placeholder="Email"
                // value=''
              />
            </AuthInputContainer>

            <AuthInputContainer style={{ width:'90%' }}>
              <AuthInput
                type="password"
                label=""
                placeholder=""
                // value=''
              />
            </AuthInputContainer>


            <AuthInputContainer style={{ width:'90%' 
          }}>
              <AuthInput
                type="password"
                label=""
                placeholder=""
                // value=''
              />
            </AuthInputContainer>
          </InputContainer>

          {/*  */}
          <div style={{ display:'flex',justifyContent:'flex-end'}}>
          <Button variant="primary" style={{margin:'3rem 2.5rem 0 0'}}></Button>{' '}
          </div>
        </CenterContainer>
        <RightContainer>

        </RightContainer>

      </MainStyle>
    </Container>
  )
}

export default SettingPage